@keyframes scroll-hint-appear {
  0% {
    transform: translateX(40px);
    opacity: 0; }
  10% {
    opacity: 1; }
  50%,
  100% {
    transform: translateX(-40px);
    opacity: 0; } }

.scroll-hint.is-right-scrollable {
